<template>
    <div class="card-inner">
        <h2>報價計算機</h2>
        <div class="grid-wrap">
            <div class="grid-row">
                <div class="grid-item layout-left">報價（支領金額）</div>
                <div class="grid-item layout-right">
                    <div>{{ data.quotation }}</div>

                    <div class="chinese-wrap">
                        <span>{{ useChineseNumber(data.quotation)[4] }}萬</span>
                        <span>{{ useChineseNumber(data.quotation)[3] }}千</span>
                        <span>{{ useChineseNumber(data.quotation)[2] }}百</span>
                        <span>{{ useChineseNumber(data.quotation)[1] }}拾</span>
                        <span>{{ useChineseNumber(data.quotation)[0] }}元</span>
                    </div>
                </div>
            </div>

            <div class="grid-row">
                <div class="grid-item layout-left">
                    <input type="checkbox" id="health_insurance_fare" name="insurance" v-model="data.health_toggle">
                    <label for="health_insurance_fare">
                        健保補充保費 2.11%
                        <!-- <br>（報價>=20K） -->
                    </label>
                </div>
                <div class="grid-item layout-right">
                    <div>{{ data.health_insurance }} (四捨五入)</div>

                    <div class="chinese-wrap">
                        <span>{{ useChineseNumber(data.health_insurance)[4] }}萬</span>
                        <span>{{ useChineseNumber(data.health_insurance)[3] }}千</span>
                        <span>{{ useChineseNumber(data.health_insurance)[2] }}百</span>
                        <span>{{ useChineseNumber(data.health_insurance)[1] }}拾</span>
                        <span>{{ useChineseNumber(data.health_insurance)[0] }}元</span>
                    </div>
                </div>
            </div>

            <div class="grid-row">
                <div class="grid-item layout-left">
                    <input type="checkbox" id="income_tax_fare" name="income_tax" v-model="data.income_tax_toggle">
                    <label for="income_tax_fare">
                        代扣所得稅 10%
                        <!-- <br>（報價>20K） -->
                    </label>
                </div>
                <div class="grid-item layout-right">
                    <div>{{ data.income_tax }} (四捨五入)</div>

                    <div class="chinese-wrap">
                        <span>{{ useChineseNumber(data.income_tax)[4] }}萬</span>
                        <span>{{ useChineseNumber(data.income_tax)[3] }}千</span>
                        <span>{{ useChineseNumber(data.income_tax)[2] }}百</span>
                        <span>{{ useChineseNumber(data.income_tax)[1] }}拾</span>
                        <span>{{ useChineseNumber(data.income_tax)[0] }}元</span>
                    </div>
                </div>
            </div>

            <div class="grid-row">
                <div class="grid-item layout-left">最終實領（支領淨額）</div>
                <div class="grid-item layout-right">
                    <div>
                        <input type="number" min="0" placeholder="請輸入金額" v-model="data.final_actual_collar">
                    </div>
                    <div class="chinese-wrap">
                        <span>{{ useChineseNumber(data.final_actual_collar)[4] }}萬</span>
                        <span>{{ useChineseNumber(data.final_actual_collar)[3] }}千</span>
                        <span>{{ useChineseNumber(data.final_actual_collar)[2] }}百</span>
                        <span>{{ useChineseNumber(data.final_actual_collar)[1] }}拾</span>
                        <span>{{ useChineseNumber(data.final_actual_collar)[0] }}元</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { computed, reactive, watch } from 'vue';
import useChineseNumber from '@/hooks/useChineseNumber'

export default {
    name: 'QuoteCalculationQuotation',
    setup() {
        /*
            報價計算機

            輸入 `實領金額` 推導出 `報價金額`。

            * `最終實領(支領淨額)` + `健保費` + `所得稅` = `報價(支領金額)`

            X/(1 -0.1 -0.0211) - 0.1X/(1 -0.1 -0.0211) - 0.0211X/(1 -0.1 -0.0211) = X
            
            * 報價（支領金額） `X/(1 -0.1 -0.0211)`  (輸出)
            * 健保費（四捨五入） `0.0211X/(1 -0.1 -0.0211)`
            * 所得稅（四捨五入） `0.1X/(1 -0.1 -0.0211)`
            * 最終實領（支領淨額） `X` (輸入)
        */


        // 數據
        let data = reactive({
            final_actual_collar: 20000, // [使用者輸入]最終實領(支領淨額)
            quotation: 0, // [計算屬性]報價(支領金額)

            health_toggle: true, // 是否勾選健保費
            health_insurance_rate: 0.0211, // [不可變更]健保費率 2021 2.11%
            health_insurance: 0, // [計算屬性]健保費

            income_tax_toggle: true,  // 是否勾選代扣所得稅
            income_tax_rate: 0.1, // [不可變更]所得稅率(報價低於兩萬不扣)
            income_tax: 0, // [計算屬性]所得稅
        })

        // 計算屬性

        // 健保費 = 0.0211 * 最終實領 / (1 -0.1 -0.0211)
        data.health_insurance = computed(()=>{
            if (data.health_toggle) {
            // if (data.health_toggle && data.quotation >= 20000) {
                return Math.round((data.health_insurance_rate * data.final_actual_collar)/(1 - data.health_insurance_rate - data.income_tax_rate));
            } else {
                return 0;
            }
        })


        // 所得稅 = 0.1 * 最終實領 / (1 -0.1 -0.0211)
        data.income_tax = computed(()=>{
            if (data.income_tax_toggle) {
            // if (data.income_tax_toggle && data.quotation > 20000) {
                return Math.round((data.income_tax_rate * data.final_actual_collar)/(1 - data.health_insurance_rate - data.income_tax_rate));
            } else {
                return 0;
            }
        })


        // 報價(支領金額) = 最終實領(支領淨額) + 健保費 + 所得稅
        data.quotation = computed(()=>{
            return Math.round(parseInt(data.final_actual_collar) + data.health_insurance + data.income_tax);
        })


        // 情況四: 監視 reactive 所定義的一個響應式數據中的某個屬性 (需用函式返回值)
        watch(()=>data.quotation, (newValue, oldValue)=>{
            console.log('data的quotation變了', newValue, oldValue)
            // let fee = data.health_insurance + data.income_tax;
            // console.log(fee, newValue)

            // if ( oldValue ) {
                
            // }



            if ( newValue < 20000 ) {
                console.log(newValue, '小於20K')
            //     console.log(`${newValue} + ${fee} = ${newValue + fee} (小於20K)`);
            //     data.health_insurance_rate = 0;
            //     data.income_tax_rate = 0;
            } 

            else if ( newValue == 20000 ) {
                console.log(newValue, '等於20K')
            //     console.log(`${newValue} + ${fee} = ${newValue + fee} (等於20K)`);
            //     data.health_insurance_rate = 0.0211;
            //     data.income_tax_rate = 0;
            }

            else if ( newValue > 20000 ) {
                console.log(newValue, '大於20K')
            //     console.log(`${newValue} + ${fee} = ${newValue + fee} (大於20K)`);
            //     data.health_insurance_rate = 0.0211;
            //     data.income_tax_rate = 0.1;
            }
        }, {immediate: true})


        


        return {
            data,

            useChineseNumber,
        }
    }
}

</script>