import { reactive } from 'vue'

export default function(num) {
    // 數據
    const chinese_list = reactive({
        0: '零',
        1: '壹',
        2: '貳',
        3: '參',
        4: '肆',
        5: '伍',
        6: '陸',
        7: '柒',
        8: '捌',
        9: '玖',
        10: '拾'
    })


    // 方法

    // 轉換為國字數字大寫
    let one = Math.trunc(num % 10);
    let ten = Math.trunc(num / 10 % 10);
    let hundred = Math.trunc(num / 100 % 10);
    let thousand = Math.trunc(num / 1000 % 10);
    let ten_thousand = Math.trunc(num / 10000 % 10);

    // 返回值
    // [ "萬", "千", "百", "拾", "個" ]
    return [chinese_list[one], chinese_list[ten], chinese_list[hundred], chinese_list[thousand], chinese_list[ten_thousand]];
}